@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300;400;700&display=swap);
.sideNav {
  color: #8FC1E3;
  flex: 1 1;
  position: fixed;
  top: 50px;
  display: block;
  background-color: #333;
  max-width: 130px;
  min-width: 100px;
  height: 50vh;
  border-radius: 0 15px 15px 0;
  transition: 0.8s ease;
}
.sideNav.collapsed {
  flex: 0 1;
  margin-left: -100px;
}

